import uniPrompt from '@/components/uni-prompt/uni-prompt';
export default {
	name: 'uniTable',
	components: {
		uniPrompt
	},
	props: {
		rank: {
			type: Number || String,
			default: 1 //0 没有 1有
		},
		thead: {
			type: Array,
			default() {
				return [
					{
						text: '项目名称',
						style: 'width: 146upx;'
					},
					{
						text: 'PM2.5',
						style: 'width: 86upx;'
					},
					{
						text: 'PM10',
						style: 'width: 93upx;'
					},
					{
						text: '公布值',
						style: 'width: 90upx;'
					},
					{
						text: '状态',
						style: 'width: 72upx;'
					},
					{
						text: '时间',
						style: 'width: 105upx;'
					},
					{
						text: '查看',
						style: 'width: 91upx;'
					}
				];
			}
		},
		tbody: {
			type: Array,
			default() {
				return [
					{
						p_td0: '锦艺四季城市花园',
						p_td1: 35,
						p_td2: 126,
						p_td3: 50,
						p_td4: '在线',
						p_td5: '16:14:25'
					}
				];
			}
		}
	},
	data() {
		return {
			list: this.tbody
		};
	},
	created() {
		this.list = this.tbody;
	},
	watch:{
		tbody(val){
			this.list = val;
		}
	},
	methods: {
		onDetail(index,item){
			this.$emit("onClickThat",{
				col_index:index,
				col_item:item
			})
		}
	}
};
